import React , { Component } from 'react'
import { AppContext , MovieContext } from '../../utils/provider'
import { apikey , apiUrl , fetchUrl } from '../../utils/api'
import queryString from 'query-string' 
import Movie from './movie'
import { Link } from 'react-router-dom'
import Pagination from 'react-js-pagination'
import Banner from './banner'
import Stagger from 'react-css-stagger';

import './pagination.css'
import './transition.css'

const menu = [
  {name: 'Now Playing' , path: 'movie/now_playing'} ,
  {name: 'Popular' , path: 'movie/popular'}
]

class Home extends Component {
  constructor(props) {
    super(props)
  }

  static contextType = MovieContext

  state = {
    menuSelect: '' ,
    loading: false  ,
    totalPages: 0 ,
    pageDisplay: 0 ,
    currentPage: 0 ,
  }

  componentDidMount() {
    const { action , totalPages , currentUrlState } = this.context
    console.log('total pages' , totalPages)
    console.log('url state' , currentUrlState)
    let currentUrl = queryString.parse(this.props.location.search)
    console.log('home url' , currentUrl.page)
    console.log('url mount' , currentUrl.page)
    if (currentUrl.page !== undefined) {
      action.setCurrentUrl(`/${currentUrl.page}`)
      console.log('update url')
      this.updateMovies(currentUrl.page , currentUrlState.path)
    } else {
      action.setCurrentUrl(`/`)
      console.log('set url to 1')
      this.updateMovies(1 , currentUrlState.path)
    }
  }

  onCartClick = item => {
    const { cart , action } = this.context 
    if (cart.includes(item)) {
      // console.log('delete')
      const indexToDelete = cart.indexOf(item)
      cart.splice(indexToDelete , 1)
      action.addToCart([...cart])
      localStorage.setItem('cart' , JSON.stringify(cart))        
    } else {
      action.addToCart([...cart , item])
      localStorage.setItem('cart' , JSON.stringify(cart))        
      // console.log('add')
    }
  }

  // render per movie
  renderMovie = movies => {
    return movies.length !== 0 ? movies.results.map((movie , i) => (
      <Movie key={i} details={movie} onClick={this.onCartClick} />
    )) : (
      <h1>Loading...</h1>
    )
  }

  // updated movies
  updateMovies = (currentPage , urlMovie) => {
    const { action } = this.context
    this.setState({ loading: true })
    console.log('updated data to ' , urlMovie)
    fetch(fetchUrl(apikey , urlMovie , `&region=ID&page=${currentPage}`))
    .then(res => res.json())
    .then(data => {
      action.setMoviesTrending(data , moviesTrending => {
        console.log('success updated data' , moviesTrending)
        console.log('current page' , currentPage)
        if (currentPage == 1) {
          this.props.history.push(`/`)   
          this.setState({ loading: false })
          this.setState({ totalPages: data.total_pages } , () => {
            console.log('total pages update' , this.state.totalPages)
          })
          this.setState({ pageDisplay: data.results.length , currentPage } , () => {
            console.log('page display' , this.state.pageDisplay)
            console.log('current page in updateMovies()' , this.state.currentPage)
          })
        } else {
          this.props.history.push(`/?page=${currentPage}`)
          this.setState({ loading: false })
          this.setState({ totalPages: data.total_pages } , () => {
            console.log('total pages update' , this.state.totalPages)
          })
          this.setState({ pageDisplay: data.results.length , currentPage } , () => {
            console.log('page display' , this.state.pageDisplay)
            console.log('current page in updateMovies()' , this.state.currentPage)
          })
        }
      })
    })
    .catch(err => console.error(err))
  }

  // handlechange per page
  handlePageChange = pageNumber => {
    const { action , currentUrlState } = this.context
    action.setCurrentPage(pageNumber , currentPage => {
      this.updateMovies(currentPage , currentUrlState.path)
    })
  }

  onMenuSelected = menu => {
    console.log('menu selecte' , menu)
    const { action , currentUrlState } = this.context
    action.setCurrentUrlState(menu , state => {
      console.log('state' , state)
      this.updateMovies(1 , state.path)
    })
  }

  render() {
    const {
      moviesTrending ,
      currentUrlState
    } = this.context
    
    const { totalPages , pageDisplay } = this.state

    return (
      <div className='w-full'>
        <Banner />
        <Menu onMenuSelected={this.onMenuSelected} currentMenu={currentUrlState} />
        { this.state.loading ? (
          <h1>loading...</h1>
        ) : (
          <React.Fragment>
            {/**
              <div>
                <div className="container mx-auto">
                  <h1 className='ml-5 mb-3 font-sans font-normal text-green'>Now playing</h1>
                </div>
                <div className="movie-container container mx-auto flex flex-wrap items-center">
                  {this.renderMovie(movies)}
                </div>
              </div>
            */}
            <div className='mt-10'>
              <div className="container mx-auto">
                <h1 className='ml-5 mb-3 font-sans font-normal text-green'>{currentUrlState.name}</h1>
              </div>
              <div className="movie-container container mx-auto flex flex-wrap items-center">
                {this.renderMovie(moviesTrending)}
              </div>
            </div>
          </React.Fragment>
        ) }
        <Pagination 
          activePage={this.state.currentPage}
          itemsCountPerPage={2} 
          totalItemsCount={totalPages}
          pageRangDisplayed={pageDisplay}
          onChange={this.handlePageChange}
        />
      </div>
    )
  }

}

class Menu extends Component {
  constructor(props) {
    super(props) 
  }

  state = {
    menuSelected: this.props.currentMenu
  }

  componentDidMount() {
    console.log('menu selected' , this.state.menuSelected)
  }

  onMenuSelect = menu => {
    this.setState({ menuSelected: menu } , () => {
      this.props.onMenuSelected(menu)
    })
  }

  render() {
    let classes = menu => this.state.menuSelected == menu
    return (
      <div className='container mx-auto flex items-center justify-center'>
        {menu.map((menu , i) => (
          <div className={`menu-list mr-5 text-md text-grey cursor-pointer ${classes(menu) ? 'active' : ''} `} onClick={() => this.onMenuSelect(menu)}>{menu.name}</div>
        ))}
        <style jsx sass>{`
          .menu-list {
            transition: all .4s ease;
            &.active {
              font-size: 23px;
              font-weight: bold;
            }
          }
        `}</style>
      </div>  
    )
  }
}

export default Home